<template>
  <vs-dialog v-model="dialogLocal" prevent-close>
    <template #header>
      <span>Registro de pagos</span>
    </template>

    <section>
      <div>
        <span>Dni:</span>
        <vs-input ref="txtdni" v-model="num_doc" id="txtdni" />
      </div>

      <div class="mt-2">
        <span>Num. Operaci贸n:</span>
        <vs-input v-model="num_operacion" type="number" />
      </div>

      <div class="mt-2">
        <span>Fecha:</span>
        <vs-input v-model="fecha" type="date" />
      </div>

      <div class=" flex flex-col mt-2">
        <span>Nombre:</span>
        <input v-model="nombre" class=" p-3 bg-gray-100 rounded-2xl" />
      </div>

      <div class="flex justify-between">
        <div class="mt-2">
          <span>Monto:</span>
          <vs-input v-model="monto" type="number" />
        </div>

        <div class="mt-2">
          <span>Monto 2:</span>
          <vs-input :disabled="true" v-model="monto_full" type="number" />
        </div>
      </div>

      <div class=" mt-2">
        <vs-checkbox v-model="asosiado" class=" font-medium">
          Es Asosiado
        </vs-checkbox>
        <span class=" text-xs"
          >(Los pagos soportar谩n la mitad de los precios si es asosiado a la
          universidad)</span
        >
      </div>

      <div class=" mt-2">
        <vs-checkbox v-model="is_donacion" class=" font-medium">
          Es Donaci贸n
        </vs-checkbox>
      </div>

      <!-- <div class=" mt-2">
        <vs-checkbox danger v-model="is_extemporaneo" class=" font-medium">
          Es Extempor谩neo
          <b class="ml-2 text-xs">({{ admisionData.fechaExtemporaneo }})</b>
        </vs-checkbox>
      </div> -->

      <div class=" mt-5">
        <span class=" text-2xl font-medium text-blue-500">Datos Exonerado</span>
        <div class=" mt-2">
          <vs-checkbox v-model="is_exonerado" class=" font-medium">
            Es Exonerado
          </vs-checkbox>
        </div>

        <div class=" flex flex-col mt-2">
          <span>Documento Referencial:</span>
          <input
            v-model="documento_referencial"
            class=" p-3 bg-gray-100 rounded-2xl"
          />
        </div>

        <div class=" w-full">
          <span class=" text-xs">Modalidad</span>
          <v-select
            v-model="modalidad"
            :options="modalidad_data"
            label="nombre"
            index="id"
          >
          </v-select>
        </div>
      </div>
    </section>

    <template #footer>
      <div class="flex justify-between focus:outline-none">
        <div class=" flex">
          <vs-button
            v-if="id && get_auth.group == 'administrador'"
            danger
            class="focus:outline-none"
            @click="delete_item"
          >
            Eliminar
          </vs-button>

          <vs-button
            v-if="id && (is_exonerado || is_donacion)"
            border
            dark
            @click="printExonerado(id)"
          >
            Imprimir
          </vs-button>
        </div>

        <div class="flex">
          <vs-button class="focus:outline-none" @click="save">
            Guardar
          </vs-button>
          <vs-button dark transparent class="focus:outline-none" @click="exit">
            Cancelar
          </vs-button>
        </div>
      </div>
    </template>
  </vs-dialog>
</template>

<script>
import axios from "@//plugins//axios.js";

export default {
  props: {
    dialog: {
      type: Boolean,
    },

    item_selected: {
      type: Object,
      default: () => {
        return {};
      },
    },
  },

  data() {
    return {
      dialogLocal: false,
      num_doc: "",
      num_operacion: "",
      nombre: "",
      monto: 0,
      monto_full: 0,
      id: 0,
      is_exonerado: true,
      documento_referencial: "",
      fecha: "",
      modalidad: {},
      modalidad_data: [],
      asosiado: false,
      is_donacion: false,
      is_extemporaneo: false,
      admisionData: {},
    };
  },

  watch: {
    dialog(newValue) {
      this.dialogLocal = newValue;
      if (newValue) {
        this.clearData();
        this.is_exonerado = true;

        setTimeout(() => {
          document.getElementById("vs-input--txtdni").focus();
        }, 100);

        this.getAdmision();

        this.set_modalidad();

        this.id = this.item_selected.id;
        this.fecha = new Date().toISOString().slice(0, 10);

        if (!this.id) {
          return;
        }

        this.$store
          .dispatch("pago/set_pago_banco_by_id", this.id)
          .then((data) => {
            if (!this.id) {
              this.is_exonerado = true;
            } else this.is_exonerado = data.is_exonerado;

            this.num_doc = data.num_doc;
            this.num_operacion = data.num_operacion;
            this.nombre = data.nombre;
            this.monto = data.monto;
            this.fecha = data.fecha;
            this.asosiado = data.asosiado;
            this.is_donacion = data.is_donacion;
            this.monto_full = data.monto_full;
            this.is_extemporaneo = data.is_extemporaneo;

            //
            if (this.modalidad_data.length <= 0) {
              this.set_modalidad().then(() => {
                this.modalidad = this.modalidad_data.find(
                  (item) => item.id == data.modalidad
                );
              });
            } else {
              this.modalidad = this.modalidad_data.find(
                (item) => item.id == data.modalidad
              );
            }

            this.documento_referencial = data.documento_referencial;
          });
      }
    },

    dialogLocal(newValue) {
      if (!newValue) {
        this.$emit("closedialog");
      }
    },

    is_exonerado(newValue) {
      if (newValue) {
        this.asosiado = false;
        this.is_donacion = false;
        this.monto = 0;
      }
    },

    asosiado(newValue) {
      if (newValue) {
        this.is_exonerado = false;
        this.is_donacion = false;
      }
    },

    is_donacion(newValue) {
      if (newValue) {
        this.is_exonerado = false;
        this.asosiado = false;
      }
    },
  },

  computed: {
    get_auth() {
      return this.$store.getters["auth/get_auth"];
    },
  },

  methods: {
    async set_modalidad() {
      try {
        var modalidad = this.$store.getters["modalidad/get_modalidad"];
        if (modalidad.length <= 0)
          await this.$store.dispatch("modalidad/set_modalidad").then((resp) => {
            modalidad = resp;
          });

        this.modalidad_data = modalidad;
      } catch (error) {
        return null;
      }
    },

    printExonerado(id) {
      if (this.is_exonerado || this.is_donacion) {
        let routeData = this.$router.resolve(`/reporte_exonerado/${id}`);

        window.open(routeData.href, "_blank");
      }
    },

    async delete_item() {
      if (this.id) {
        const { status } = await axios.delete(`pagos_banko/${this.id}`);
        if (status == 204) {
          this.$vs.notification({
            title: "Registro de Pagos",
            text: "Eliminado correctamente! ",
            color: "success",
          });
        } else {
          this.$vs.notification({
            title: "Registro de Pagos",
            text: "No se pudo eliminar! ",
            color: "danger",
          });
        }
        this.$emit("actualizar");
        this.exit();
      }
    },

    activarDesactivarBoolens() {
      if (this.asosiado) {
        this.is_exonerado = false;
        this.is_donacion = false;
      }

      if (this.is_exonerado) {
        this.asosiado = false;
        this.is_donacion = false;
      }

      if (this.is_donacion) {
        this.asosiado = false;
        this.is_exonerado = false;
      }
    },

    validar() {
      if (
        this.num_doc.length < 8 ||
        this.num_operacion.length < 5 ||
        this.nombre.length < 10 ||
        this.monto.toString().length < 3
      )
        return false;

      return true;
    },

    async getAdmision() {
      // no se sabe
      if (this.item_selected.id) return;
      const { data } = await axios.get("admision");
      this.admisionData = data[0];
      const { fechaExtemporaneo } = this.admisionData;
      const fechaActual = Date().now;
      if (fechaExtemporaneo == fechaActual) {
        this.is_extemporaneo = true;
      }
    },

    async save() {
      if (!this.validar) {
        return;
      }

      let sede = "";
      if (this.get_auth.group == "sede") sede = this.get_auth.username;

      if (this.asosiado) {
        this.is_exonerado = false;
        this.is_donacion = false;
      }

      if (this.is_exonerado) {
        this.asosiado = false;
        this.is_donacion = false;
      }

      if (this.is_donacion) {
        this.asosiado = false;
        this.is_exonerado = false;
      }

      const obj = {
        dni: this.num_doc,
        num_ticket: this.num_operacion,
        monto: this.monto,
        id: this.id,
        nombre: this.nombre,
        is_exonerado: this.is_exonerado,
        documento_referencial: this.documento_referencial,
        modalidad: this.modalidad ? this.modalidad.id : null,
        asosiado: this.asosiado,
        fecha: this.fecha,
        sede: sede,
        is_donacion: this.is_donacion,
        monto_full: this.monto,
        is_extemporaneo: this.is_extemporaneo,
      };

      try {
        let response = null;
        if (this.id) {
          delete obj.sede;
          delete obj.monto_full;

          await this.$store.dispatch("pago/update_pago", [obj]).then((res) => {
            response = res;
          });
        } else {
          obj.fecha = this.fecha;
          await this.$store.dispatch("pago/add_pago", [obj]).then((res) => {
            response = res;
          });
        }

        if (response != null) {
          if (response == "exonerado-repetido") {
            this.$vs.notification({
              title: "Registro de Pagos",
              text:
                "Ya existe un pago con el mismo dni en la modalidad de exonerado! う锔",
              color: "danger",
            });
            return;
          }

          this.$vs.notification({
            title: "Registro de Pagos",
            text: "Los cambios se guardaron correctamente! ",
            color: "success",
          });

          this.printExonerado(response[0].id);

          this.$emit("actualizar");
          this.exit();
        } else {
          this.$vs.notification({
            title: "Registro de Pagos",
            text: "No se pudo realizar los cambios! ",
            color: "danger",
          });
        }
      } catch (error) {
        //
        this.$vs.notification({
          title: "Registro de Pagos",
          text: "Ocurrio un error! ",
          color: "danger",
        });
      }
    },

    exit() {
      this.$emit("closedialog");
      this.dialogLocal = false;
    },

    clearData() {
      this.num_doc = "";
      this.num_operacion = "";
      this.nombre = "";
      this.monto = 0;
      this.monto_full = 0;
      this.id = 0;
      this.is_exonerado = false;
      this.documento_referencial = "";
      this.fecha = "";
      this.modalidad = {};
    },
  },
};
</script>

<style lang="scss" scoped></style>
